        '',
        '',
        '',
        '',
        ''
      ],
      years: [
        '',
        '2017',
        '2016',
        '2015',
        '2014',
        '2013-2011',
        '2010-2006',
        '2005-2000',
        '90',
        '80',
        ''
      ],
      feeTypes: ['', '', ''],
      videos: []
    }
  },
  beforeRouteUpdate(to, from, next) {
    this.updateVideos()
    next()
  },
  methods: {
    updateVideos: function() {
      let self = this
      self.loading = true
      getVideos(100).then(function(res) {
        self.videos = res
        self.loading = false
      })
    },
    typeChange: function(value) {
      this.updateVideos()
    },
    feeTypeChange: function(value) {
      this.updateVideos()
    },
    regionChange: function(value) {
      this.updateVideos()
    },
    yearChange: function(value) {
      this.updateVideos()
    }
  },
  components: {
    TopLoading,
    VideoCard,
    SelectPiece
  },
  mounted() {
    this.updateVideos()
    console.log(this.$router)
  }
}
</script>
